input[type="button"],
input[type="submit"],
input[type="search"],
input[type="reset"] {
    -webkit-appearance: none;
}

textarea {
    -webkit-appearance: none;
}

::-webkit-input-placeholder {
    /* WebKit browsers */
    color: #c7c7cc;
    font-size: 16px;
    text-align: left;
}

:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #c7c7cc;
    text-align: left;
    font-size: 16px;
}

::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #dedede;
    text-align: left;
    font-size: 16px;
}

:-ms-input-placeholder {
    /* Internet Explorer 10+ */
    color: #dedede;
    text-align: left;
    font-size: 16px;
}

.smllist {
    clear: both;
    width: 200%;
    -webkit-transform: scale(0.5);
    -webkit-transform-origin: 0 0;
    transform: scale(0.5);
    transform-origin: 0 0;
    border-bottom: 1px solid #ccc;
}
